var content={about:{p1:' is an athlete, yoga instructor & health and wellness consultant. She has lived and worked at several impact centers over the world, teaching health and wellness workshops and working on communitary projects to empower underprivelged villages and schools in Uganda, Tanzania, India, Vietnam and Argentina. While continuing her research on Healing Arts and Alternative Therapies, she worked at ecovillages, sustainable living and women empowering projects all over East Africa.',p2:'Natali holds a degree in Psychology from University of Washington Seattle, and has been working as a counselor with at risk populations for over 5 years taking part on several social entrepreneurship projects. She\'s trained in evidence based therapies, trauma informed care, positive youth development, nutrition, fitness training & business fundamentals. She obtained certifications as a Reiki Level II practitioner and 200hr Ashtanga Vynasa Yoga Intructor in India and continues to expand her knowledge during her travels.',p3:'Her dream is to use her passion and devotion for sports to inspire women towards a healthier lifestyle, freedom and self-reliance. To empower, educate and enable women from all over the world to fight for their rights, pursue their dreams and reach their maximum potential. Having come from nothing, a small town in a suburb in Buenos Aires, and having turned the many obstacles ahead of her into blessings and opportunities to grow, she is a true believer that anything is possible in this life. '},home:{p1:'Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32.'},blog:{posts:[{p1:'M I N D F U L E S S  We all heard the say, if you cant defeat your enemy, join them. Most times we let our minds take over and control our lives in a negative way. In fact, most things we do, habits we adopt, the things we say, happen unconsciously and in automatic pilot. What if instead of seeing our mind as an enemy, we see it as an amazing tool we can use to achieve and materialize things that you want in life. We can choose to see negativity as our enemy created by a confused, blurred mind. Because when you and your mind come into harmony, life will take a different turn.',p2:' 10 years ago, I was trapped in my own head, filled with fears and insecurities that sometimes try to hunt me nowadays. Coming from a small little village with very humble beginnings and no one believing Id make it out of there except my parents, who wouldve imagined Id be ever traveling the world learning lots and lots by the minute, having the opportunities to understand other cultures and their practices and having such a long journey with infinite posibilites ahead. Walking along this colorful streets, surrounded by beautiful, green mountains, feeling so small and so big at the same time. Who wouldve thought, who wouldve thought. Imagination and dreams are free, the only limits lie within yourself...'}]}};var fbLink='https://www.facebook.com/Nomad-Yoga-Meditation-Reiki-132243704300765/?hc_ref=ARSwmwMabO66qzEVPId0P4K9gwrg18GVMuPLTxNGStcb5bQWo1r5gqEsLemV8I4GGOI&fref=nf';

